.header {
  position: relative;
  width: 100%;
  height: 150px;
  background-color: #e9fbff;
  box-shadow: 0px 2px 6px 0px rgba(0, 0, 0, 0.2); }

.header .container {
  font-size: 0; }

.header__left, .header__right {
  font-size: 16px;
  display: inline-block;
  width: 50%; }

.logo {
  position: absolute;
  top: 13%;
  left: 8%;
  width: 300px;
  height: 65%;
  color: #3432a7; }
  .logo h1 {
    margin: 0;
    padding: 0;
    font-weight: 400;
    font-style: normal;
    font-size: 48px;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3); }
  .logo span {
    margin: 0;
    padding: 0;
    font-style: italic;
    font-size: 20px;
    text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.3); }

.header__descr {
  position: absolute;
  top: 16%;
  width: 42%;
  height: 105px;
  background: url(../../img/futuristic-technology.jpg) 89%;
  background-size: 100%;
  border: 2px solid #3432a7;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5); }

.footer {
  position: relative;
  width: 100%;
  height: 100px;
  background-color: #e9fbff;
  box-shadow: 0px -1px 6px 0px rgba(0, 0, 0, 0.3); }

.content {
  float: right;
  width: 75%; }

.menu {
  float: left;
  width: 24%;
  border-right: 2px solid #3432a7;
  padding-right: 4%;
  margin-top: 3%;
  height: 223px; }

.nav {
  list-style: none;
  text-align: right;
  font-size: 21px;
  font-weight: 300;
  font-style: normal;
  letter-spacing: 1px;
  color: #3432a7; }

.nav-item-active {
  font-weight: 400;
  text-shadow: 1px 2px 2px rgba(0, 0, 0, 0.3); }

.nav-item:hover {
  font-weight: 400;
  text-shadow: 1px 2px 2px rgba(0, 0, 0, 0.3); }

/*	Open Sans, 300, 300 italic, 400, 400 italic, 600, 600 italic, 700, 700 italic, 800, 800 italic*/
* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box; }

html {
  position: relative;
  font-size: 16px; }
  @media screen and (max-width: 1200px) {
    html {
      font-size: 16px; } }
  @media screen and (max-width: 992px) {
    html {
      font-size: 15px; } }
  @media screen and (max-width: 768px) {
    html {
      font-size: 14px; } }
  @media screen and (max-width: 480px) {
    html {
      font-size: 13px; } }

html, body {
  min-height: 100%;
  font-family: Open Sans, Optima, sans-serif;
  width: 100%;
  line-height: 1.42; }

body {
  background-color: #ffffff; }

.section {
  min-height: 100%;
  overflow: hidden;
  max-width: 1200px;
  padding: 0 10px;
  margin: 0 auto;
  padding-bottom: 150px; }

.empty {
  height: 150px; }

